//
// Pagination
// --------------------------------------------------

// Genearl
.pagination,
.entry-navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

// Pagination specific
.pagination {
  .pages {
    display: block;
    margin: 0;
    padding: 0;
    list-style: none;
    > li {
      display: inline-block;
      width: $pagination-link-height;
      margin-bottom: 0;
      color: $gray-dark;
      font: {
        size: $font-size-md;
        weight: bold;
      }
      text-align: center;
      > a {
        display: block;
        width: $pagination-link-height;
        height: $pagination-link-height;
        transition: all .3s;
        color: $gray-dark;
        line-height: $pagination-link-height;
        text-decoration: none;
      }
      &.active > a {
        color: $primary;
        cursor: default;
        pointer-events: none;
        &:hover { box-shadow: none; }
      }
    }
  }
  .prev-btn,
  .next-btn {
    display: inline-block;
    height: $pagination-link-height;
    padding: 0 15px;
    transition: all .3s;
    color: $gray-dark;
    font: {
      size: $font-size-button-md;
      weight: $btn-font-weight;
    }
    letter-spacing: $btn-letter-spacing;
    line-height: $pagination-link-height;
    text: {
      transform: $btn-text-transform;
      decoration: none;
    }
    > i {
      display: inline-block;
      margin-top: -1px;
      font-size: 1.3em;
      vertical-align: middle;
    }
  }
  .prev-btn {
    padding-left: 10px;
    > i { margin-right: 2px; }
  }
  .next-btn {
    padding-right: 10px;
    > i { margin-left: 2px; }
  }
  .pages > li > a,
  .prev-btn, .next-btn {
    &:hover {
      background-color: $white;
      box-shadow: $box-shadow-pagination-link;
    }
    &:active {
      transition: all .1s;
      background-color: $gray-lighter;
      box-shadow: none;
    }
  }
}
// Firefox alignment fix
@-moz-document url-prefix() {
  .pagination {
    .prev-btn > i,
    .next-btn > i { margin-top: -2px; }
  }
}
// Edge alignment fix
@supports (-ms-ime-align: auto) {
  .pagination {
    .prev-btn > i,
    .next-btn > i { margin-top: -2px; }
  }
}

// Entry Navigation specific
.entry-navigation {
  align-items: flex-start;
  .prev-btn,
  .back-btn,
  .next-btn {
    display: block;
    min-height: $navigation-link-height;
    color: $gray-dark;
  }
  .prev-btn .label,
  .back-btn,
  .next-btn .label {
    height: $navigation-link-height;
    background-color: $white;
    line-height: $navigation-link-height;
    text-decoration: none;
    box-shadow: $box-shadow-pagination-link;
  }
  .prev-btn, .next-btn {
    position: relative;
    width: 50%;
    .label {
      display: inline-block;
      position: absolute;
      top: 0;
      padding: 0 16px;
      transition: opacity .2s;
      color: $gray-dark;
      font: {
        size: $font-size-button-md;
        weight: $btn-font-weight;
      }
      letter-spacing: $btn-letter-spacing;
      text-transform: $btn-text-transform;
      z-index: 5;
      > i {
        display: inline-block;
        margin-top: -1px;
        font-size: 1.3em;
        opacity: .4;
        vertical-align: middle;
      }
    }
    .post-preview {
      display: table;
      width: 100%;
      transition: opacity .2s;
      opacity: 0;
      > .post-preview-thumb,
      > .post-preview-details {
        display: table-cell;
        vertical-align: middle;
      }
      > .post-preview-thumb {
        > img {
          display: block;
          width: 100%;
        }
        @media (max-width: 480px) { display: none; }
      }
      > .post-preview-details {
        text-decoration: none;
        .post-preview-title {
          display: block;
          margin-bottom: 2px;
          font: {
            size: $font-size-sm;
            weight: bold;
          }
        }
        .post-preview-meta {
          display: block;
          color: $gray;
          font: {
            size: floor($font-size-base * .6875);
            weight: 600;
          }
          > i {
            display: inline-block;
            margin: {
              top: -1px;
              right: 5px;
            }
            font-size: 1.1em;
            vertical-align: middle;
          }
        }
      }
    }
    .label:hover {
      opacity: 0;
      & + .post-preview { opacity: 1; }
    }
  }
  .prev-btn {
    padding-right: 10px;
    .label {
      left: 0;
      padding-left: 13px;
      > i { margin-right: 5px; }
    }
    .post-preview > .post-preview-thumb {
      width: ($navigation-link-height + 12);
      padding-right: 12px;
    }
  }
  .next-btn {
    padding-left: 10px;
    .label {
      right: 0;
      padding-right: 13px;
      > i { margin-left: 5px; }
    }
    .post-preview {
      text-align: right;
      > .post-preview-thumb {
        width: ($navigation-link-height + 13);
        padding-left: 13px;
      }
    }
  }
  .back-btn {
    width: $navigation-link-height;
    min-width: $navigation-link-height;
    transition: all .3s;
    font-size: 21px;
    text-align: center;
    &:hover {
      background-color: $gray-lighter;
      box-shadow: none;
    }
  }
}
