//
// Dropdown (Override some default Bootstrap styles)
// --------------------------------------------------

.dropdown-toggle::after,
.dropright .dropdown-toggle::after,
.dropleft .dropdown-toggle::before {
  margin-top: -1px;
  vertical-align: middle;
}
.dropright .dropdown-toggle::after {
  margin-top: -2px;
  margin-left: 4px;
  border-left: .4em solid;
}
.dropleft .dropdown-toggle::before {
  margin-top: -2px;
  margin-right: 4px;
}
.dropdown-menu {
  padding: 0;
  border: 1px solid $border-color;
  border-radius: 0;
  box-shadow: $box-shadow-dropdown;
}
.dropdown-header,
.dropdown-item,
.dropdown-item-text {
  min-width: 190px;
  padding: 12px 20px;
  > i {
    display: inline-block;
    vertical-align: middle;
  }
}
.lang-dropdown {
  .dropdown-header,
  .dropdown-item,
  .dropdown-item-text { min-width: 140px; }
}
.dropdown-item,
li.dropdown-item > a,
.dropdown-item-text {
  margin: 0;
  transition: color .3s;
  border-top: 1px solid $border-color;
  color: $gray-darker;
  font: {
    size: floor($font-size-base * .8125);
    weight: 600;
  }
  text: {
    decoration: none;
  }
  &:focus { background-color: transparent; }
  &:first-child { border-top: 0; }
  > img {
    display: inline-block;
    width: 20px;
    margin: {
      top: -1px;
      right: 6px;
    }
    vertical-align: middle;
  }
}
.dropdown-item,
li.dropdown-item > a {
  &:hover, &.active {
    background-color: transparent;
    color: $primary;
  }
}
li.dropdown-item {
  padding: 0;
  > a {
    display: block;
    padding: 12px 20px;
  }
  &:hover, &.active {
    > a, > a::after { color: $primary !important; }
  }
}
.dropdown-item.has-children {
  position: relative;
  > a {
    position: relative;
    padding-right: 32px;
    &::after {
      display: block;
      position: absolute;
      top: 50%;
      right: 12px;
      margin-top: -12px;
      transition: color .3s;
      color: $gray-darker;
      font: {
        family: Feather;
        size: $font-size-base;
        weight: normal;
      }
      content: '\e930';
    }
  }
  > .dropdown-menu {
    top: 0;
    left: 100%;
    margin-top: -1px;
  }
  &:hover > .dropdown-menu {
    display: block;
    animation: dropdown-slide-right .3s;
  }
}
.right-aligned .dropdown-item.has-children {
  > .dropdown-menu {
    right: 100%;
    left: auto;
  }
  &:hover > .dropdown-menu {
    animation: dropdown-slide-left .3s;
  }
}
.dropdown-item-text {
  &:hover, &.active { color: $gray-darker; }
}

.dropdown-header {
  color: $gray;
  font: {
    family: $font-family-base;
    size: floor($font-size-base * .8125);
    weight: 700;
  }
  letter-spacing: .03em;
  > i { margin-top: -2px; }
}

.dropdown-divider { display: none; }

// Mega Dropdown
.mega-dropdown {
  padding: 0;
  .column {
    width: 220px;
    padding: 30px 15px;
    &:first-child { margin-left: 15px; }
    &:last-child { margin-right: 15px; }
  }
  .widget {
    margin: 0;
    .widget-title {
      color: $gray-dark;
      font: {
        family: $font-family-base;
        size: floor($font-size-base * .8125);
        weight: 600;
      }
      &::after {
        bottom: -2px;
        height: 2px;
      }
    }
    &.widget-custom-menu {
      ul > li > a {
        font-size: floor($font-size-base * .8125);
      }
    }
  }
}

// Show Animation
.show .dropdown-menu {
  animation: dropdown-show .25s;
}
@keyframes dropdown-show {
  from { opacity: 0; }
  to { opacity: 1; }
}
@keyframes dropdown-slide-down {
  from {
    transform: translateY(-7px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes dropdown-slide-right {
  from {
    transform: translateX(-7px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes dropdown-slide-left {
  from {
    transform: translateX(7px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
