//
// Market Buttons
// --------------------------------------------------

.market-btn {
  display: inline-block;
  padding: 5px 14px 5px 45px;
  transition: all .3s;
  border: 1px solid $border-color;
  background: {
    position: center left 12px;
    color: $white;
    size: 24px 24px;
    repeat: no-repeat;
  }
  text-decoration: none;
  &:hover {
    box-shadow: $box-shadow-base;
  }
  &:active {
    transition: all .15s;
    background-color: lighten($gray-lighter, 1%);
    box-shadow: $box-shadow-base-pressed;
  }
  .mb-subtitle {
    display: block;
    margin-bottom: -4px;
    color: $gray;
    font-size: $font-size-sm;
  }
  .mb-title {
    display: block;
    color: $gray-dark;
    font-size: $font-size-xl;
  }

  // App Store
  &.apple-btn {
    background-image: url(data:image/svg+xml;utf8;base64,);
  }
  
  // Google Play
  &.google-btn {
    background-image: url(data:image/svg+xml;utf8;base64,);
  }

  // Windows Store
  &.windows-btn {
    background-image: url(data:image/svg+xml;utf8;base64,);
  }

  // Blackberry Store
  &.blackberry-btn {
    background-image: url(data:image/svg+xml;utf8;base64,);
  }

  // Amazom Market
  &.amazon-btn {
    background-image: url(data:image/png;base64,);
  }

  // Dark Version
  &.market-btn-dark {
    border-color: $gray-darker;
    background-color: $gray-darker;
    box-shadow: none !important;
    .mb-subtitle,
    .mb-title { color: $white; }
    .mb-subtitle { opacity: .6; }
    &:hover {
      border-color: darken($gray-darker, 5%);
      background-color: darken($gray-darker, 5%);
    }
    &:active {
      border-color: darken($gray-darker, 10%);
      background-color: darken($gray-darker, 10%);
    }
  }

  // Light Skin
  &.market-btn-light-skin {
    border-color: $border-light-color;
    background-color: transparent;
    box-shadow: none !important;
    .mb-subtitle,
    .mb-title { color: $white; }
    .mb-subtitle { opacity: .6; }
    &:hover { background-color: rgba($white, .06); }
  }

  &.market-btn-dark,
  &.market-btn-light-skin {
    &.apple-btn {
      background-image: url(data:image/svg+xml;utf8;base64,);
    }
    &.amazon-btn {
      background-image: url(data:image/png;base64,);
    }
    &.blackberry-btn {
      background-image: url(data:image/svg+xml;utf8;base64,);
    }
  }
}
