//
// Off-Canvas
// --------------------------------------------------

// Static / Trigered Container
.offcanvas-container.is-static,
.offcanvas-container.is-triggered {
  display: block;
  position: fixed;
  top: 25px;
  left: 0;
  width: $offcanvas-width;
  height: calc(100% - 50px);
  padding: {
    top: floor($grid-vertical-step * 1.75);
    bottom: $grid-vertical-step;
  }
  transition: left .4s cubic-bezier(.785, .135, .15, .86) .3s, box-shadow .3s .3s;
  background-color: $white;
  box-shadow: $box-shadow-offcanvas;
  z-index: 1100;
  & + .offcanvas-wrapper { padding-left: ($offcanvas-width + 15); }
  &.offcanvas-container-reverse {
    right: 0;
    left: auto;
    transition: right .4s cubic-bezier(.785, .135, .15, .86) .3s, box-shadow .3s .3s;
    box-shadow: $box-shadow-offcanvas-reverse;
    & + .offcanvas-wrapper {
      padding: {
        right: ($offcanvas-width + 15);
        left: 0;
      }
    }
  }
  .offcanvas-scrollable-area {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow-y: auto;
    -ms-overflow-style: none;
    &::-webkit-scrollbar { display: none; }
  }
}
.offcanvas-container.is-triggered {
  top: 0;
  left: -($offcanvas-width);
  height: 100%;
  box-shadow: none;
  &.in-view {
    left: 0;
    box-shadow: $box-shadow-offcanvas;
  }
  &.offcanvas-container-reverse {
    right: -($offcanvas-width);
    left: auto;
    box-shadow: none;
    &.in-view {
      right: 0;
      box-shadow: $box-shadow-offcanvas-reverse;
    }
  }
  .offcanvas-close { display: block; }
}
@media (max-width: 1480px) {
  .offcanvas-container.is-static {
    width: ($offcanvas-width - 50);
    & + .offcanvas-wrapper {
      padding-left: (($offcanvas-width + 15) - 50);
    }
    &.offcanvas-container-reverse + .offcanvas-wrapper {
      padding: {
        right: (($offcanvas-width + 15) - 50);
        left: 0;
      }
    }
  }
}

// Offcanvas Footer
.offcanvas-footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  border-top: 1px solid $border-color;
  background-color: $white;
}

// Toggle
.offcanvas-toggle {
  display: none;
  position: fixed;
  top: 50%;
  left: 0;
  width: ($btn-height-md + 3);
  height: $btn-height-md;
  margin-top: -($btn-height-md / 2);
  padding-right: 3px;
  transition: color .3s;
  background-color: $white;
  color: $body-color;
  font-size: 22px;
  line-height: $btn-height-md;
  box-shadow: 0 12px 18.8px 1.2px rgba($gray-dark, .2);
  cursor: pointer;
  z-index: 1090;
  text: {
    align: center;
    decoration: none;
  }
  > i {
    position: relative;
    z-index: 5;
  }
  &::after {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 3px;
    height: 100%;
    transition: width .25s;
    background-color: $primary;
    content: '';
    z-index: 1;
  }
  &:hover {
    color: $white;
    &::after { width: 100%; }
  }
  &.offcanvas-toggle-reverse {
    right: 0;
    left: auto;
    padding: {
      right: 0;
      left: 3px;
    }
    &::after {
      right: auto;
      left: 0;
    }
  }
}

// Off-Canvas Close
.offcanvas-close {
  display: none;
  position: absolute;
  top: 8px;
  right: 13px;
  color: $gray-dark;
  font-size: 22px;
  cursor: pointer;
}

@media (max-width: $screen-lg) {
  .offcanvas-toggle,
  .offcanvas-close { display: block; }
  .offcanvas-container,
  .offcanvas-container.is-static,
  .offcanvas-container.is-triggered {
    display: block;
    position: fixed;
    top: 0;
    left: -($offcanvas-width);
    width: $offcanvas-width;
    height: 100%;
    padding: {
      top: floor($grid-vertical-step * 1.75);
      bottom: $grid-vertical-step;
    }
    transition: left .4s cubic-bezier(.785, .135, .15, .86) .3s, box-shadow .3s .3s;
    background-color: $white;
    z-index: 1100;
    overflow-y: auto;
    &.in-view {
      left: 0;
      box-shadow: $box-shadow-offcanvas;
    }
    &.offcanvas-container-reverse {
      right: -($offcanvas-width);
      left: auto;
      transition: right .4s cubic-bezier(.785, .135, .15, .86) .3s, box-shadow .3s .3s;
      &.in-view {
        right: 0;
        box-shadow: $box-shadow-offcanvas-reverse;
      }
    }
    & + .offcanvas-wrapper {
      padding: {
        right: 0 !important;
        left: 0 !important;
      }
    }
    .offcanvas-scrollable-area {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      overflow-y: auto;
      -ms-overflow-style: none;
      &::-webkit-scrollbar { display: none; }
    }
  }
  .page-title.page-title-indent-left,
  .page-title.page-title-indent-right {
    padding: {
      right: 0 !important;
      left: 0 !important;
    }
  }
}
